.input-container{-webkit-box-flex:0;-ms-flex:0 0 auto;flex:0 0 auto;margin-top:16px}.input-container__input,.input-container__label{font-family:Roboto;font-style:normal;font-weight:300;font-size:14px;line-height:16px;color:#121212;text-align:left}.input-container__input{width:224px;padding-left:8px;padding-right:16px;margin-left:8px;border-bottom:1px solid #999}.input-container__input_disable{pointer-events:none}.input-container__clean-input{background:rgba(0,0,0,0);-webkit-transform:translateX(-200%) translateY(0);-ms-transform:translateX(-200%) translateY(0);transform:translateX(-200%) translateY(0)}.input-container__fake{display:inline-block;width:8px;height:8px}@media (max-width:767px){.input-container{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.input-container__input{margin-left:0;margin-top:10px}.input-container__clean-input{background:rgba(0,0,0,0);width:8px;height:8px;-webkit-transform:translateX(212px) translateY(-200%);-ms-transform:translateX(212px) translateY(-200%);transform:translateX(212px) translateY(-200%)}}