.checkbox{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;margin-bottom:8px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse;margin-right:16px;font-family:Roboto;font-style:normal;font-weight:300;font-size:14px;line-height:16px;color:#999;text-align:left;cursor:pointer}.checkbox input{opacity:0;cursor:pointer}.checkbox__checkmark{position:relative;top:0;left:0;width:12px;height:12px;border:1px solid #999}.checkbox:hover input~.checkbox__checkmark{background-color:#ccc}.checkbox input:checked~.checkbox__checkmark{border-color:#0ec261}.checkbox__checkmark:after{content:"";position:absolute;display:none}.checkbox input:checked~.checkbox__checkmark:after{display:block}.checkbox__checkmark:after{margin-top:-1px;margin-left:2px;width:13px;height:10px;background:url(assets/icon/check.svg);background-size:cover}